/* eslint-disable no-alert */
import React, { Component } from 'react';
import '@transferwise/neptune-css/dist/css/flex.css';

import Drawer from './Drawer';
import { generateCodeBlock, generateInput, generateState } from '../../docs/utils';
import { Button } from '../index';

const OPENDIRECTION = [
  { label: 'Right', value: 'right' },
  { label: 'Left', value: 'left' },
  { label: 'Top', value: 'top' },
  { label: 'Bottom', value: 'bottom' },
];

const KNOBS = {
  knobs: [
    { type: 'checkbox', label: 'Open', state: 'open', defaultState: false },
    {
      type: 'select',
      label: 'Open Direction',
      state: 'openDirection',
      options: OPENDIRECTION,
      defaultState: { ...OPENDIRECTION[0] },
    },
  ],
};

export default class DrawerDocs extends Component {
  state = { ...generateState(KNOBS) };
  render() {
    const { open, openDirection } = this.state;
    const styleContent = {
      width: openDirection.value === 'left' || openDirection.value === 'right' ? 720 : '100%',
      height: openDirection.value === 'top' || openDirection.value === 'bottom' ? 170 : '100%',
    };
    return (
      <div className="container">
        <section className="section" id="drawer">
          <div className="row">
            <div className="col-md-6">
              <h2>Drawer</h2>
              <span role="img" aria-label="uhm">
                When a Slider meets a Dimmer
              </span>
              {generateCodeBlock('Drawer', KNOBS, this)}
            </div>
            <div className="col-md-6">
              <div className="col-xs-12">
                <Drawer
                  open={open}
                  openDirection={openDirection.value}
                  onClose={() => this.setState({ open: false })}
                  footerContent={
                    <Button onClick={() => alert('Action clicked')} label="Action" block />
                  }
                  headerTitle="A title"
                >
                  <div style={{ ...styleContent }}>
                    <p>
                      Cat ipsum dolor sit amet, purr when being pet. Fight own tail weigh eight
                      pounds but take up a full-size bed fall over dead (not really but gets
                      sypathy) love you, then bite you or sit in box. Spill litter box, scratch at
                      owner, destroy all furniture, especially couch allways wanting food or chase
                      the pig around the house bite nose of your human. Cats eat all the power cords
                      chew on cable for i can haz for cat gets stuck in tree firefighters try to get
                      cat down firefighters get stuck in tree cat eats firefighters slippers.Cat
                      ipsum dolor sit amet, purr when being pet. Fight own tail weigh eight pounds
                      but take up a full-size bed fall over dead (not really but gets sypathy) love
                      you, then bite you or sit in box. Spill litter box, scratch at owner, destroy
                      all furniture, especially couch allways wanting food or chase the pig around
                      the house bite nose of your human. Cats eat all the power cords chew on cable
                      for i can haz for cat gets stuck in tree firefighters try to get cat down
                      firefighters get stuck in tree cat eats firefighters slippers.
                    </p>
                  </div>
                </Drawer>
                <div className="row">{KNOBS.knobs.map(knob => generateInput(knob, this))}</div>
              </div>
            </div>
          </div>
        </section>
      </div>
    );
  }
}



